<template>
	<div class="addEdit-block">
		<el-form
			class="add-update-preview"
			ref="ruleForm"
			:model="ruleForm"
			:rules="rules"
			label-width="180px"
		>
			<template >
				<el-form-item class="input" v-if="type!='info'"  label="" prop="gongsimingcheng" >
					<el-input v-model="ruleForm.gongsimingcheng" placeholder="" clearable  :readonly="ro.gongsimingcheng"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="gongsimingcheng" >
					<el-input v-model="ruleForm.gongsimingcheng" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="" prop="gonghao" >
					<el-input v-model="ruleForm.gonghao" placeholder="" clearable  :readonly="ro.gonghao"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="gonghao" >
					<el-input v-model="ruleForm.gonghao" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="" prop="qiyemingcheng" >
					<el-input v-model="ruleForm.qiyemingcheng" placeholder="" clearable  :readonly="ro.qiyemingcheng"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="qiyemingcheng" >
					<el-input v-model="ruleForm.qiyemingcheng" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="upload" v-if="type!='info' && !ro.zhaopinfengmian" label="" prop="zhaopinfengmian" >
					<file-upload
						tip=""
						action="file/upload"
						:limit="3"
						:multiple="true"
						:fileUrls="ruleForm.zhaopinfengmian?ruleForm.zhaopinfengmian:''"
						@change="zhaopinfengmianUploadChange"
					></file-upload>
				</el-form-item>
				<el-form-item class="upload" v-else-if="ruleForm.zhaopinfengmian" label="" prop="zhaopinfengmian" >
					<img v-if="ruleForm.zhaopinfengmian.substring(0,4)=='http'&&ruleForm.zhaopinfengmian.split(',w').length>1" class="upload-img" style="margin-right:20px;" v-bind:key="index" :src="ruleForm.zhaopinfengmian" width="100" height="100">
					<img v-else-if="ruleForm.zhaopinfengmian.substring(0,4)=='http'" class="upload-img" style="margin-right:20px;" v-bind:key="index" :src="ruleForm.zhaopinfengmian.split(',')[0]" width="100" height="100">
					<img v-else class="upload-img" style="margin-right:20px;" v-bind:key="index" v-for="(item,index) in ruleForm.zhaopinfengmian.split(',')" :src="$base.url+item" width="100" height="100">
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="" prop="zhiweimingcheng" >
					<el-input v-model="ruleForm.zhiweimingcheng" placeholder="" clearable  :readonly="ro.zhiweimingcheng"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="zhiweimingcheng" >
					<el-input v-model="ruleForm.zhiweimingcheng" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="select" v-if="type!='info'"  label="" prop="zhiweileixing" >
					<el-select :disabled="ro.zhiweileixing" v-model="ruleForm.zhiweileixing" placeholder="" >
						<el-option
							v-for="(item,index) in zhiweileixingOptions"
							v-bind:key="index"
							:label="item"
							:value="item">
						</el-option>
					</el-select>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="zhiweileixing" >
					<el-input v-model="ruleForm.zhiweileixing"
						placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="" prop="xinzifanwei" >
					<el-input v-model.number="ruleForm.xinzifanwei" placeholder="" clearable  :readonly="ro.xinzifanwei"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="xinzifanwei" >
					<el-input v-model="ruleForm.xinzifanwei" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="" prop="suoshuxingye" >
					<el-input v-model="ruleForm.suoshuxingye" placeholder="" clearable  :readonly="ro.suoshuxingye"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="suoshuxingye" >
					<el-input v-model="ruleForm.suoshuxingye" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="" prop="fulidaiyu" >
					<el-input v-model="ruleForm.fulidaiyu" placeholder="" clearable  :readonly="ro.fulidaiyu"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="fulidaiyu" >
					<el-input v-model="ruleForm.fulidaiyu" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="" prop="gongsidizhi" >
					<el-input v-model="ruleForm.gongsidizhi" placeholder="" clearable  :readonly="ro.gongsidizhi"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="gongsidizhi" >
					<el-input v-model="ruleForm.gongsidizhi" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="date" v-if="type!='info'" label="" prop="faburiqi" >
					<el-date-picker
						format="yyyy  MM  dd "
						value-format="yyyy-MM-dd"
						v-model="ruleForm.faburiqi" 
						type="date"
						:readonly="ro.faburiqi"
						placeholder=""
					></el-date-picker> 
				</el-form-item>
				<el-form-item class="input" v-else-if="ruleForm.faburiqi" label="" prop="faburiqi" >
					<el-input v-model="ruleForm.faburiqi" placeholder="" readonly></el-input>
				</el-form-item>
			</template>
			<el-form-item class="textarea" v-if="type!='info'" label="" prop="gongsijianjie" >
				<el-input
					style="min-width: 200px; max-width: 600px;"
					type="textarea"
					:rows="8"
					placeholder=""
					v-model="ruleForm.gongsijianjie" >
				</el-input>
			</el-form-item>
			<el-form-item v-else-if="ruleForm.gongsijianjie" label="" prop="gongsijianjie" >
				<span class="text">{{ruleForm.gongsijianjie}}</span>
			</el-form-item>
			<el-form-item class="btn">
				<el-button class="btn3"  v-if="type!='info'" type="success" @click="onSubmit">
					<span class="icon iconfont icon-xihuan"></span>
					
				</el-button>
				<el-button class="btn4" v-if="type!='info'" type="success" @click="back()">
					<span class="icon iconfont icon-xihuan"></span>
					
				</el-button>
				<el-button class="btn5" v-if="type=='info'" type="success" @click="back()">
					<span class="icon iconfont icon-xihuan"></span>
					
				</el-button>
			</el-form-item>
		</el-form>
    

	</div>
</template>
<script>
	import { 
		isIntNumer,
	} from "@/utils/validate";
	export default {
		data() {
			var validateIntNumber = (rule, value, callback) => {
				if(!value){
					callback();
				} else if (!isIntNumer(value)) {
					callback(new Error(""));
				} else {
					callback();
				}
			};
			return {
				id: '',
				type: '',
			
			
				ro:{
					gongsimingcheng : false,
					gonghao : false,
					qiyemingcheng : false,
					zhaopinfengmian : false,
					zhiweimingcheng : false,
					zhiweileixing : false,
					xinzifanwei : false,
					suoshuxingye : false,
					fulidaiyu : false,
					gongsidizhi : false,
					faburiqi : false,
					gongsijianjie : false,
					sfsh : false,
					shhf : false,
					clicktime : false,
					discussnum : false,
					storeupnum : false,
				},
			
				ruleForm: {
					gongsimingcheng: '',
					gonghao: '',
					qiyemingcheng: '',
					zhaopinfengmian: '',
					zhiweimingcheng: '',
					zhiweileixing: '',
					xinzifanwei: '',
					suoshuxingye: '',
					fulidaiyu: '',
					gongsidizhi: '',
					faburiqi: '',
					gongsijianjie: '',
					shhf: '',
					clicktime: '',
				},
				zhiweileixingOptions: [],

				rules: {
					gongsimingcheng: [
						{ required: true, message: '', trigger: 'blur' },
					],
					gonghao: [
					],
					qiyemingcheng: [
					],
					zhaopinfengmian: [
					],
					zhiweimingcheng: [
					],
					zhiweileixing: [
					],
					xinzifanwei: [
						{ validator: validateIntNumber, trigger: 'blur' },
					],
					suoshuxingye: [
					],
					fulidaiyu: [
					],
					gongsidizhi: [
					],
					faburiqi: [
					],
					gongsijianjie: [
					],
					sfsh: [
					],
					shhf: [
					],
					clicktime: [
					],
					discussnum: [
						{ validator: validateIntNumber, trigger: 'blur' },
					],
					storeupnum: [
						{ validator: validateIntNumber, trigger: 'blur' },
					],
				},
			};
		},
		props: ["parent"],
		computed: {



		},
		components: {
		},
		created() {
			this.ruleForm.faburiqi = this.getCurDate()
		},
		methods: {
			// 
			download(file){
				window.open(`${file}`)
			},
			// 
			init(id,type) {
				if (id) {
					this.id = id;
					this.type = type;
				}
				if(this.type=='info'||this.type=='else'||this.type=='msg'){
					this.info(id);
				}else if(this.type=='logistics'){
					for(let x in this.ro) {
						this.ro[x] = true
					}
					this.logistics=false;
					this.info(id);
				}else if(this.type=='cross'){
					var obj = this.$storage.getObj('crossObj');
					for (var o in obj){
						if(o=='gongsimingcheng'){
							this.ruleForm.gongsimingcheng = obj[o];
							this.ro.gongsimingcheng = true;
							continue;
						}
						if(o=='gonghao'){
							this.ruleForm.gonghao = obj[o];
							this.ro.gonghao = true;
							continue;
						}
						if(o=='qiyemingcheng'){
							this.ruleForm.qiyemingcheng = obj[o];
							this.ro.qiyemingcheng = true;
							continue;
						}
						if(o=='zhaopinfengmian'){
							this.ruleForm.zhaopinfengmian = obj[o];
							this.ro.zhaopinfengmian = true;
							continue;
						}
						if(o=='zhiweimingcheng'){
							this.ruleForm.zhiweimingcheng = obj[o];
							this.ro.zhiweimingcheng = true;
							continue;
						}
						if(o=='zhiweileixing'){
							this.ruleForm.zhiweileixing = obj[o];
							this.ro.zhiweileixing = true;
							continue;
						}
						if(o=='xinzifanwei'){
							this.ruleForm.xinzifanwei = obj[o];
							this.ro.xinzifanwei = true;
							continue;
						}
						if(o=='suoshuxingye'){
							this.ruleForm.suoshuxingye = obj[o];
							this.ro.suoshuxingye = true;
							continue;
						}
						if(o=='fulidaiyu'){
							this.ruleForm.fulidaiyu = obj[o];
							this.ro.fulidaiyu = true;
							continue;
						}
						if(o=='gongsidizhi'){
							this.ruleForm.gongsidizhi = obj[o];
							this.ro.gongsidizhi = true;
							continue;
						}
						if(o=='faburiqi'){
							this.ruleForm.faburiqi = obj[o];
							this.ro.faburiqi = true;
							continue;
						}
						if(o=='gongsijianjie'){
							this.ruleForm.gongsijianjie = obj[o];
							this.ro.gongsijianjie = true;
							continue;
						}
						if(o=='clicktime'){
							this.ruleForm.clicktime = obj[o];
							this.ro.clicktime = true;
							continue;
						}
						if(o=='discussnum'){
							this.ruleForm.discussnum = obj[o];
							this.ro.discussnum = true;
							continue;
						}
						if(o=='storeupnum'){
							this.ruleForm.storeupnum = obj[o];
							this.ro.storeupnum = true;
							continue;
						}
					}
				}
				// 
				this.$http({
					url: `${this.$storage.get('sessionTable')}/session`,
					method: "get"
				}).then(({ data }) => {
					if (data && data.code === 0) {
						var json = data.data;
						if(((json.gonghao!=''&&json.gonghao) || json.gonghao==0) && this.$storage.get("role")!=""){
							this.ruleForm.gonghao = json.gonghao
							this.ro.gonghao = true;
						}
						if(((json.qiyemingcheng!=''&&json.qiyemingcheng) || json.qiyemingcheng==0) && this.$storage.get("role")!=""){
							this.ruleForm.qiyemingcheng = json.qiyemingcheng
							this.ro.qiyemingcheng = true;
						}
					} else {
						this.$message.error(data.msg);
					}
				});
				this.$http({
					url: `option/zhiweileixing/zhiweileixing`,
					method: "get"
				}).then(({ data }) => {
					if (data && data.code === 0) {
						this.zhiweileixingOptions = data.data;
					} else {
						this.$message.error(data.msg);
					}
				});
			
			},
			// 

			info(id) {
				this.$http({
					url: `zhaopinxinxi/info/${id}`,
					method: "get"
				}).then(({ data }) => {
					if (data && data.code === 0) {
						this.ruleForm = data.data;
						//
						let reg=new RegExp('../../../upload','g')//g
					} else {
						this.$message.error(data.msg);
					}
				});
			},

			// 
			async onSubmit() {
					if(this.ruleForm.zhaopinfengmian!=null) {
						this.ruleForm.zhaopinfengmian = this.ruleForm.zhaopinfengmian.replace(new RegExp(this.$base.url,"g"),"");
					}
					var objcross = this.$storage.getObj('crossObj');
					if(!this.ruleForm.id) {
						delete this.ruleForm.userid
					}
					await this.$refs["ruleForm"].validate(async valid => {
						if (valid) {
							if(this.type=='cross'){
								var statusColumnName = this.$storage.get('statusColumnName');
								var statusColumnValue = this.$storage.get('statusColumnValue');
								if(statusColumnName!='') {
									var obj = this.$storage.getObj('crossObj');
									if(statusColumnName && !statusColumnName.startsWith("[")) {
										for (var o in obj){
											if(o==statusColumnName){
												obj[o] = statusColumnValue;
											}
										}
										var table = this.$storage.get('crossTable');
										await this.$http({
											url: `${table}/update`,
											method: "post",
											data: obj
										}).then(({ data }) => {});
									}
								}
							}
							
							await this.$http({
								url: `zhaopinxinxi/${!this.ruleForm.id ? "save" : "update"}`,
								method: "post",
								data: this.ruleForm
							}).then(async ({ data }) => {
								if (data && data.code === 0) {
									this.$message({
										message: "",
										type: "success",
										duration: 1500,
										onClose: () => {
											this.parent.showFlag = true;
											this.parent.addOrUpdateFlag = false;
											this.parent.zhaopinxinxiCrossAddOrUpdateFlag = false;
											this.parent.search();
											this.parent.contentStyleChange();
										}
									});
								} else {
									this.$message.error(data.msg);
								}
							});
						}
					});
			},
			// uuid
			getUUID () {
				return new Date().getTime();
			},
			// 
			back() {
				this.parent.showFlag = true;
				this.parent.addOrUpdateFlag = false;
				this.parent.zhaopinxinxiCrossAddOrUpdateFlag = false;
				this.parent.contentStyleChange();
			},
			zhaopinfengmianUploadChange(fileUrls) {
				this.ruleForm.zhaopinfengmian = fileUrls;
			},
		}
	};
</script>
<style lang="scss" scoped>
